<template>
    <div class='home'>
        <!-- 整体容器 -->
        <el-container>
            <!-- 侧边栏区域 -->
            <el-aside :width="toggleMenu ? '0px':'180px'">
                <h5>
                    <p v-show='!toggleMenu'>侧边栏</p>
                </h5>
                <el-menu background-color="#333" text-color="#fff" active-text-color="#409BFF" :unique-opened='true'
                    :collapse='toggleMenu' :collapse-transition='false' :router='true' :default-active="activePath">
                    <!-- 一级菜单 -->
                    <el-submenu :index="items.id" v-for='items in menuList' :key='items.id'>
                        <!-- 一级菜单模板区域 -->
                        <template slot="title">
                            <i :class='items.icon'></i>
                            <span>{{items.titleName}}</span>
                        </template>
                        <!-- 二级菜单 -->
                        <el-menu-item :index="'/'+item.path" v-for='item in items.children' :key='item.id'
                            @click="keepMenuState('/'+item.path)">
                            <template slot="title">
                                <i class="el-icon-menu"></i>
                                <span>{{item.titleName}}</span>
                            </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <!-- 右侧主体区域 -->
            <el-container>
                <!-- 头部区域 -->
                <el-header>
                    <div class="header-left">
                        <i class='iconfont icon-switch' @click='hangdleToggleMenu' title='展开/收缩'></i>
                    </div>
                    <div class="header_center">
                        <span>教材征订系统</span>
                    </div>
                    <div class="header_right" animation-interation-count='infinite'>
                        <el-badge :value="message" class="item message">
                            <el-button type="info" icon="el-icon-message" class='mesbtn' circle></el-button>
                        </el-badge>
                        <span> <i class='iconq el-icon-full-screen' title='全屏' @mouseenter="enter" @mouseleave="leave"
                                ref='quanping' @click="fullScreen"></i></span>
                        <img :src="titleImg" class='animated rotateIn' @click='loadImg'>
                        <el-dropdown class='dropdown'>
                            <span class="el-dropdown-link">
                              设置<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                              <el-dropdown-item divided><span @click='loginOut'>退出登陆</span></el-dropdown-item>
                            </el-dropdown-menu>
                          </el-dropdown>
                          
                    </div>
                </el-header>
                <!-- 图片上传对话框 -->
                <el-dialog title="头像" :visible="changeUserText" width="30%" :before-close="handleClose">
                    <el-upload class="avatar-uploader upload" :show-file-list="false"
                        action="https://jsonplaceholder.typicode.com/posts/" :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar loadSize">
                        <i v-else class="el-icon-folder-add avatar-uploader-icon loadSize"></i>
                    </el-upload>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="changeUserText = false">取 消</el-button>
                        <el-button type="primary" @click="upload">上 传</el-button>
                    </span>
                </el-dialog>
                <!-- 右侧内容区域 -->
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    import axios from 'axios'
    import screenfull from 'screenfull'
    export default {
        data() {
            return {
                message:'0',
                titleImg:require('../assets/image/img0.jpg'),
                imageUrl: '',
                menuList: [],
                toggleMenu: false,
                //从sessionStorage中取出的path值
                activePath: '',
                isFullscreen: false,
                changeUserText:false,
                username:''
            }
        },
        methods: {
            loginOut() {
                console.log(1)
                window.sessionStorage.clear()
                this.$router.push('/login')
                setTimeout(() => {
                    this.$message({
                        message: this.username+'退出成功',
                        type: 'success'
                    });
                }, 500);
            },
            getMenuList() {
                axios.get('/api/json').then((res) => {
                    const data = res.data.data.data
                    this.menuList = data.menu
                })
            }, hangdleToggleMenu() {      //点击按钮，切换侧边栏的折叠与展开
                this.toggleMenu = !this.toggleMenu
            }, keepMenuState(activePath) {      //保存链接的激活状态
                window.sessionStorage.setItem('activePath', activePath)
                this.activePath = activePath
            }, enter: function () {
                this.$refs.quanping.style.transform = 'scale(1.2)'
            }, leave: function () {
                this.$refs.quanping.style.transform = 'scale(1)'
            }, fullScreen() {   //全屏
                screenfull.toggle()
            },loadImg(){    //打开头像对话框
                this.changeUserText=true
            },handleClose(){    //头像对话框关闭按钮
                this.changeUserText=false
            }, handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');  
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
      },upload(){
          this.changeUserText=false 
          this.titleImg=this.imageUrl
      }
        }, created() {
            this.getMenuList()
            this.activePath = window.sessionStorage.getItem('activePath')
            this.username = window.sessionStorage.getItem('username')
        }
    }
</script>

<style lang='stylus' scoped>
    @import '.././assets/stylus/variable.styl';
    .loadSize
        width 100px
        font-size 50px
        border-radius 50%
    .el-container
        .el-aside
            background-color $bgcolor
            border-right $bgcolor
            transition all ease .5s
            h5 
                min-width 50px
                display flex 
                justify-content center 
                align-items center
                color #fff
            .el-menu
                border none
        .el-header
            background-color $bgcolor
            display flex 
            justify-content space-between 
            align-items center
            .header-left
                color #fff 
                .iconfont
                    font-size 30px
                    cursor pointer
            .header_center
                span 
                    font-size $sizej
                    color $colorw
                    width 100%
            .header_right
                display flex 
                justify-content space-around 
                align-items center
                span
                    margin-right 1rem
                    .iconq
                        font-size 20px
                        color #fff
                        transition all ease .5s
                        cursor pointer
                img 
                    width 40px
                    border-radius 50%
                    margin-right 20px 
                    cursor pointer
</style>